<template>
    <div>
        <div class="panel panel-box">
            <div class="panel-heading">
                注意事项
            </div>
            <div class="panel-body">
                <table class="table">
                    <tr>
                        <td style="width: 100px; text-align: center !important;">{{optitle}}</td>
                        <td>
                            <glui-editor :content.sync="opcontent" :action="uploadurl" :height="300" :width="500"></glui-editor>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
        <tpldescription ref="booktips" :productid="productid" modeltype="3" pname="温馨提示"></tpldescription>
    </div>
</template>
<script>
import tpldescription from "@/components/tpldescription"
import httputils from "@/js/httputils"
export default {
    components: { tpldescription },
    data() {
        return {
            optitle: "同程OP注意事项",
            opcontent: "",
            productid: $("#productid").val(),
            uploadurl: $("#uploadurl").val()
        }
    },
    mounted() {
        this.loadDatas();
    },
    methods: {
        /**
         * 获取op注意事项内容
         */
        getopdatas() {
            let that = this;
            return new Promise((resolve, reject) => {
                httputils.get('/travel/supergoproduct/ProductBaseInfoExtends/GetOPAnnouncements',
                    {
                        ProductId: that.productid
                    })
                    .then((result) => {
                        if (result && result.IsSuccess) {
                            that.opcontent = result.Result || "";
                            resolve();
                        } else {
                            reject(`${that.optitle}:${result.ErrorMsg}`)
                        }
                    }).catch((err) => {
                        reject(`${that.optitle}加载异常`)
                    });
            });
        },
        /**
         * 保存op注意事项内容
         */
        saveopdatas() {
            let that = this;
            return new Promise((resolve, reject) => {
                httputils.post('/travel/supergoproduct/ProductBaseInfoExtends/SaveOPAnnouncements', {
                    oPAnnouncements: that.opcontent,
                    productId: that.productid
                }).then((result) => {
                    resolve({ success: result && result.IsSuccess, msg: `${that.optitle}:${result.ErrorMsg}` });
                }).catch((err) => {
                    resolve({ success: false, msg: `${that.optitle}:${err}` });
                });
            })
        },
        /**
         * 加载全部内容
         */
        loadDatas() {
            let that = this;
            const loading = that.$loading({ lock: true });
            Promise.all([
                that.getopdatas(),
                that.$refs["booktips"].loadDatas()
            ]).then((result) => {
                loading && loading.close();
            }).catch((err) => {
                loading && loading.close();
                this.$message({
                    type: 'error',
                    message: err
                });
            });
        },
        /**
         * 保存全部内容
         */
        saveDatas() {
            let that = this;
            const loading = that.$loading({ lock: true });
            that.$refs["booktips"].checkTplData().then((result) => {
                return Promise.all([
                    that.saveopdatas(),
                    that.$refs["booktips"].saveTplData()
                ]);
            }).then(resList => {
                loading && loading.close();
                //处理结果呈现
                var result = {
                    success: true,
                    msg: ''
                };
                resList.forEach((v, i) => {
                    result.success = result.success && v.success;
                    v.msg && (result.msg += `<p>${v.msg}</p>`);
                });
                that.$confirm(result.msg, "保存结果", {
                    confirmButtonText: "确定",
                    type: result.success ? "success" : "warning",
                    center: true,
                    showCancelButton: false,
                    dangerouslyUseHTMLString: true
                }).then(_ => {

                });
            }).catch((err) => {
                this.$message({
                    type: 'error',
                    message: err
                })
                loading && loading.close();
            });
        }
    }
}
</script>




