<template>
    <div class='panel panel-box'>
        <div class='panel-heading'>
            {{tplName}}（
            <font color='red'>*</font> 必填）
            <span style="float: right;">
                <select v-model="tplSelected" v-on:change="changeTpl">
                    <option v-for='tpl in tpls' :value='tpl.Id' :key="tpl.Id">
                        {{tpl.Name}}
                    </option>
                </select>
            </span>
        </div>
        <div class='panel-body'>
            <table class="table">
                <tbody>
                    <tr v-for='model in models' :key="model.Name">
                        <td style="width: 100px;text-align: center !important;">
                            {{model.Name}}
                        </td>
                        <td>
                            <ul class="tplGroup">
                                <li v-for='(group,index) in model.Groups' :key="index">
                                    <ul class="tplLine">
                                        <li v-for='line in group.Lines' :key='line.LineId'>
                                            <div :class="group.ContentType == 1 ? 'radio' : 'checkbox'">
                                                <label :class='{checked:line.IsScelected}'>
                                                    <input v-if="group.ContentType == 1" type="radio" :value="line.LineId" v-model='group.RadioValue' v-on:change="radioChange(group,line)" :name="'radio' + group.GroupId">
                                                    <input v-else type="checkbox" :true-value='true' :false-value='false' v-model='line.IsScelected'>
                                                </label>
                                                <span v-for='(column,index) in line.Details' :key="index">
                                                    <select v-if="column.ControlType == 1" v-model="column.ChoseValue">
                                                        <option v-for="(opt,key) in column.SLTValues" :key='key' :value="opt">{{ opt }}</option>
                                                    </select>
                                                    <input v-else-if="column.ControlType == 4" type="text" :placeholder="column.TipValue" v-model="column.ChoseValue" />
                                                    <textarea v-else-if="column.ControlType == 5" :placeholder="column.TipValue" v-model="column.ChoseValue"></textarea> {{ column.ControlType!=1&&column.ControlType!=4&&column.ControlType!=5? column.TipValue:''}}
                                                </span>
                                            </div>
                                        </li>
                                    </ul>
                                </li>
                            </ul>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
import httputils from "@/js/httputils";
export default {
    props: {
        productid: {
            default: 0,
            type: [Number, String]
        },
        modeltype: {
            default: 0,
            type: [Number, String]
        },
        pname: {
            default: "",
            type: [Number, String]
        }
    },
    data: function () {
        return {
            tpls: [],
            models: [],
            tplSelected: 0,
            tplName: ""
        };
    },
    mounted() {
        var that = this;
        that.tplName = that.pname;
    },
    watch: {
        tplSelected: function (val, old) {
            if (!val) {
                this.tplSelected = old;
            }
        }
    },
    methods: {
        loadDatas: function (templateId) {
            var that = this;
            return new Promise((resolve, reject) => {
                httputils
                    .get(
                        "/travel/supergoproduct/ProductExtendInfoConfig/GetNoticeTemplate",
                        {
                            noticeType: that.modeltype,
                            templateId: templateId,
                            productId: that.productid,
                            t: new Date().getTime()
                        }
                    )
                    .then(function (response) {
                        if (response && response.IsSuccess) {
                            that.models = response.Modules;
                            that.tplSelected =
                                response.Modules && response.Modules.length >= 1
                                    ? response.Modules[0].TemplateId
                                    : 0;
                            that.tpls = response.Templates;
                            resolve();
                        } else {
                            reject(`${that.tplName}:${response.ErrorMsg}`);
                        }
                    }).catch(err => {
                        reject(`${that.tplName}加载异常`);
                    });
            });
        },
        radioChange: function (group, line) {
            line.IsScelected = group.RadioValue == line.LineId;
            group.Lines.forEach(function (item) {
                if (item.LineId != line.LineId) {
                    item.IsScelected = false;
                }
            });
        },
        changeTpl: function () {
            var that = this;
            if (!that.tplSelected || that.tplSelected <= 0) {
                that.tplSelected =
                    that.tpls && that.tpls.length >= 2 ? that.tpls[1].value : 0;
            }
            that.loadDatas(that.tplSelected);
        },
        checkTplData: function () {
            var that = this;
            return new Promise((resolve, reject) => {
                $("[aria-validate='required']").each((i, v) => {
                    if (v.tagName.toLowerCase() == 'input' && v.type.toLowerCase() == 'text' && v.value == '') {
                        $(v).focus();
                        reject(`请检查${that.tplName}输入内容是否完整`);
                        return false;
                    }
                    if (v.tagName.toLowerCase() == 'select' && v.type.toLowerCase() == 'select-one' && v.value == '') {
                        $(v).focus();
                        reject(`请检查${that.tplName}输入内容是否完整`);
                        return false;
                    }
                    if (v.tagName.toLowerCase() == 'textarea' && v.type.toLowerCase() == 'textarea' && v.value == '') {
                        $(v).focus();
                        reject(`请检查${that.tplName}输入内容是否完整`);
                        return false;
                    }
                });
                resolve(true);
            });
        },
        saveTplData: function (callback) {
            var that = this;
            return new Promise((resolve, reject) => {
                var chooserData = [];
                var controlData = [];
                if (that.models) {
                    that.models.forEach(function (model) {
                        if (model.Groups) {
                            model.Groups.forEach(function (group) {
                                if (group.Lines) {
                                    group.Lines.forEach(function (line) {
                                        if (line.IsScelected) {
                                            chooserData.push({
                                                GroupId: group.GroupId,
                                                LineId: line.LineId,
                                                ModelId: model.ModelId
                                            });
                                            if (line.Details) {
                                                line.Details.forEach(function (column) {
                                                    controlData.push({
                                                        ControllerKey: column.Unique,
                                                        ControllerValue: column.ChoseValue
                                                    });
                                                });
                                            }
                                        }
                                    });
                                }
                            });
                        }
                    });
                }
                if (that.modeltype != 7 && (chooserData.length <= 0 || controlData.length <= 0)) {
                    reject(`${that.pname}未选择任何有效内容`)
                    return;
                }
                httputils.post(
                    "/travel/supergoproduct/ProductExtendInfoConfig/JsonSaveProductModelAndTips",
                    {
                        productId: that.productid,
                        modelType: that.modeltype,
                        TempId: that.tplSelected,
                        chooserData: chooserData,
                        controlData: controlData,
                        t: new Date().getTime()
                    }).then(resp => {
                            resolve({ success: resp && resp.IsSuccess, msg: `${that.tplName}:${resp.Msg}` });
                    }).catch(err => {
                        resolve({ success: false, msg: `${that.tplName}:${err}` });
                    });
            });
        }
    }
}
</script>
<style scoped>
</style>





