import Vue from "vue";
import App from "./App";
import './index.css'

const app = new Vue({
  template: '<App  ref="producttips"/>',
  components: {
    App
  }
}).$mount("#app");

$(function () {
  $("#saveProductInfo").click(function () {
    app.$refs["producttips"].saveDatas();
  });
});



